@import 'variables';


* {
    box-sizing: border-box;
    padding: 0;
    margin: 0;
    scroll-behavior: smooth;

}

html{
    overflow-x: hidden;
}

body {
    background: $color-bg;
    overflow-x: hidden;
}

a {
    text-decoration: none;
    color: $text-color;
}

.bg {

    /* ff 3.6+ */
    background: -moz-radial-gradient(circle at 3% 35%, $light-gradiant 0%, $dark-gradiant 100%);

    /* safari 5.1+,chrome 10+ */
    background: -webkit-radial-gradient(circle at 3% 35%, $light-gradiant 0%, $dark-gradiant 100%);

    /* opera 11.10+ */
    background: -o-radial-gradient(circle at 3% 35%, $light-gradiant 0%, $dark-gradiant 100%);

    /* ie 10+ */
    background: -ms-radial-gradient(circle at 3% 35%, $light-gradiant 0%, $dark-gradiant 100%);

    /* global 92%+ browsers support */
    background: radial-gradient(circle at 3% 35%, $light-gradiant 0%, $dark-gradiant 100%);

}

